import React from 'react';
import axios from 'axios';
import '../Styles/promjenaSifre.css';
//import decode from 'jwt-decode';
import Api from './api';

export default class PromjenaSifre extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			stara: '',
			nova: ''
		}

		this.promjenaSifre = this.promjenaSifre.bind(this);
		this.handlePass = this.handlePass.bind(this);
	}

	handlePass(e) {
		this.setState({ [e.target.name]: e.target.value })
	}

	promjenaSifre(e) {
		e.preventDefault();
		axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
		Api.put(`api/users/chpw/${document.cookie}`, {
			oldPassword: this.state.stara,
			newPassword: this.state.nova,
		})
		.then(res => {
			if(res.status === 202) {
				alert("Uspješna izmjena šifre");
			}
			else {
				alert("Greška pri promjeni šifre");
			}
		})
		.catch(err => {
			console.log(err);
		});
	}

	componentDidMount(e) {
	}

	render() {
		return(
			<div className="sifra" >
				<h1>Promjena šifre</h1>
				<form onSubmit={this.promjenaSifre} >
					<label>Stara šifra</label>
					<input type="password" placeholder="Stara šifra" name="stara" onChange={this.handlePass} />
					<label>Nova šifra</label>
					<input type="password" placeholder="Nova šifra" name="nova" onChange={this.handlePass} />
					<button type="submit" className="dugme2" value="Promijenite šifru">
						<i className="fas fa-check"></i>Promijenite šifru
					</button> 
				</form>
			</div>
		);
	}
}


