import React from 'react';
import '../Styles/promjenaSifre.css';
import axios from 'axios';
import Api from './api';

export default class DodajModeratora extends React.Component {
    
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      email: ''
    }

    this.dodaj = this.dodaj.bind(this);
    this.handleUsername = this.handleUsername.bind(this);
    this.handlePassword = this.handlePassword.bind(this);
    this.handleMail = this.handleMail.bind(this);
  }

  dodaj(e) {
    e.preventDefault();
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.post("api/registration", {
      user: {
        username: this.state.username,
        password: this.state.password,
        email: this.state.email
      }
    })
    .then(function(res) {
      if(res.status === 200) {
        alert("Moderator registrovan!");
        self.props.history.push("/cms/index/moderatori");
      }
      else {
        alert("Moderator nije registrovan!");
      }
    })
    .catch(function(err) {
      alert("Greška");
    })
  }

  handleUsername(e) {
    this.setState({ username: e.target.value });
  }

  handlePassword(e) {
    this.setState({ password: e.target.value });
  }

  handleMail(e) {
    this.setState({ email: e.target.value });
  }

  render() {
    return(
      <div className="sifra" >
        <form className="dodajModeratora" onSubmit={this.dodaj} >
          <label>Username</label>
          <input 
            type="text" 
            placeholder="Username"
            onChange={this.handleUsername}
            value={this.state.username}
            required
          />
          <label>Password</label>
          <input 
            type="password"
            placeholder="Password" 
            onChange={this.handlePassword}
            value={this.state.password}
            required
          />
          <label>Email</label>
          <input 
            type="email"
            placeholder="Email"
            onChange={this.handleMail}
            value={this.state.email}
            required
          />
          <button type="submit" className="dugme2" >Dodaj moderatora</button>
        </form>
      </div>
    );
  }
}


