import React from 'react';
import { Link, } from 'react-router-dom';
import axios from 'axios';
import Api from './api';

import '../Styles/moderatori.css';

export default class Moderatori extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      search: '',
      focus: false,
      moderatori: [],
      permission: true,
    }

    this.removeModerator = this.removeModerator.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
  }

  handleSearch(e) {
    this.setState({ search: e.target.value });
  }

  removeModerator(i) {
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.delete("api/users/" + i)
    .then(res => {
      if(res.status === 200) {
        alert("Moderator obrisan!");
        this.props.history.push(res.data.redirect);
      }
      else {
        alert("Greska pri brisanju");
      }
    })
    .catch(err => {
      alert("Greška")
    })
  }

  componentDidMount() {
    var self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/users")
    .then(function(res) {
      self.setState({ moderatori: res.data.user});
    })
    .catch(function(err) {
      alert("Greška");
    });

    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get('api/users/' + document.cookie)
    .then(res => {
      if(res.data.user.permission === "Moderator") {
        this.setState({
          permission: false
        });
      }
    })
    .catch(err => {
      alert("Greška");
    })
  }

  render() {
    return(
      <div className="moderatori" >
        <h1>Moderatori</h1>
        <input 
          type="text" 
          placeholder="Search" 
          className="search" 
          onChange={this.handleSearch}
          onFocus={() => this.setState({ focus: true })}
          onBlur={() => this.setState({ focus: false })}
          style={this.state.focus ? { borderBottom: "2px solid darkblue" } : null}
        />
        <table>
          <tr>
            <td className="ime">Ime</td>
            <td className="edituj">Edituj</td>
            <td className="obrisi">Obriši</td>
          </tr>
          {this.state.moderatori.filter(moderator => moderator.username.toLowerCase()
            .includes(this.state.search.toLowerCase()))
          .map((name, i) => { 
            return <tr key={i} >
                    <td>{name.username}</td>  
                    <td>
                      {this.state.permission === true ? 
                        <Link 
                          to={`/cms/index/moderator/${name._id}`} 
                          className="editModeratora" 
                        >
                          <i className="fas fa-edit"></i>
                        </Link>
                        : <button onClick={() => alert("Nije moguce editovati moderatora!")}><i className="fas fa-times"></i></button>
                      }
                    </td>
                    <td>
                      {this.state.permission ?
                        <button onClick={() => this.removeModerator(name._id)} ><i className="fas fa-trash"></i></button>
                        : <button onClick={() => alert("Nije moguce obrisati moderatora!")}><i className="fas fa-times"></i></button>
                      }
                    </td>
                  </tr>
          })}
        </table>
        <div className="linkDodaj" >
        { this.state.permission ?
          <Link to="/cms/index/dodajModeratora" className="link" ><i className="fas fa-plus-square"></i>Dodaj Moderatora</Link>
          :
          null
        }
          </div>
      </div>
    );
  }
}


