import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../HeaderComponent/header';

import './onama.css';


class Onama extends Component {
  render() {
    return (
    	<div className="informacije">
				<div className="fixano">
					<Header />
				</div>
				<div className="main">
					<div id="opac">\
						<h3> Još od 1987. godine pogrebne usluge Martinović su tu da Vam pruže podršku u najbolnijim trenucima.</h3>
						<h4>Uvažavajući Vaše zahtjeve i potrebe formiramo uslugu kojom možete završiti kompletnu organizaciju sahrane vaših najmilijih. Naši dežurni brojevi telefona su vam dostupni 24 sata na dan.</h4>
					</div>
				</div>
			</div>
    );
  }
}

export default Onama;


