import React from 'react';
import Header from '../HeaderComponent/header';
import Api from '../api';
import axios from 'axios';
import Footer from '../footer/Fotter';

import './forma.css';

export default class Forma extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			ime: '',
			prezime: '',
			telefon: '',
			email: '',
			grad: '',
			adresa: '',
			premIme: '',
			premPrezime: '',
			datumRodj: '',
			datumSmrti: '',
			mjRodj: '',
			mjSmrti: '',
			religija: '',
			premium: false,
			slika: [],
			dat1: '',
			dat1set: false,
			dat2: '',
			dat2set: false,
		}

		this.handleSubmit = this.handleSubmit.bind(this);
		this.handlePicture = this.handlePicture.bind(this);
		this.naruci = this.naruci.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	handleReligija = (e) => {
		this.setState({ religija: e.target.value })
	}

	handleClick = () => {
		this.setState({ dat1set: false });
	}

	handleClick2 = () => {
		this.setState({ dat2set: false });
	}

	handleDate = (e) => {
		this.setState({ datumRodj: e.target.value });
		let a = e.target.value;
		a = a.split('-');
		a = a.reverse();
		console.log(a);
		this.setState({
			dat1: a.join('/'),
			dat1set: true,
		});
	}

	handleDate2 = (e) => {
		this.setState({ datumSmrti: e.target.value });
		let a = e.target.value;
		a = a.split('-');
		a = a.reverse();
		console.log(a);
		this.setState({ dat2: a.join('/'), dat2set: true });
	}

	handleChange(e) {
		this.setState({ [e.target.name]: e.target.value });
	}

	naruci(e) {
		e.preventDefault();
		let d = new Date(this.state.datumSmrti);
		d = d.toISOString();
		let dR = new Date(this.state.datumRodj);
		dR = dR.toISOString();
		console.log(e.target.value);
		console.log(this.state);
		Api.post("api/memories/new", {
			memory: {
			  personalInfo: {
				name: {
				  first: this.state.ime,
				  last: this.state.prezime
				},
				phone: this.state.telefon,
				email: this.state.email,
				place: {
				  city: this.state.grad,
				  address: this.state.adresa
				}
			  },
			  deceased: {
				name: {
				  first: this.state.premIme,
				  last: this.state.premPrezime
				},
				biography: this.state.biografija,
				religion: this.state.religija,
				dateOfBirth: dR,
				dateOfDeath: d,
				birthPlace: this.state.mjRodj,
				placeOfDeath: this.state.mjSmrti,
				photo: this.state.slika,
			  }
			}
		  })
		  .then(res => {
				if(res.status === 200) {
					alert("Uspjesna narudzba!");
				}
				else {
					console.log(res);
					alert("Greska pri narucivanju");
				}
		  })
		  .catch(err => {
				alert("Greska");
		  });
	}

	handlePicture(e) {
		console.log(e.target);
		let self = this;
		axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
		const data = new FormData();
		data.append('file', e.target.files[0]);
		/*const config = {
		headers: {
			'Accept': 'application/json',
			'Content-Type': 'multipart/form-data'
		}
		}*/
		Api.post('api/picture', data)
		.then(res => {
		console.log(res);
		if(res.status === 200) {
					console.log(res.data.photo[0])
			self.setState({ slika: [res.data.photo[0]] });
				}
		})
		.catch(err => {
		console.log(err);
		});
	}
	
	handleSubmit(e) {
		e.preventDefault();
		console.log('submit');
	}

	render() {
		return(
			<div className="narucistr">
				<Header />
				<div className="marginaForme">
					<div>
						<header id="formahead">
							<h1 className="formanaslov">uspomene.ba</h1>
							<br/> <br/>
							<h1 className="narudzbausluga">Narudžba usluga</h1>

						</header>
					</div>
					<div className="formaa">
						<form>
							<h2>Podaci o naručiocu</h2>
							<div class="form-row">
								<div class="form-group col-md-4">
									<label for="inputEmail4">Ime</label>
									<input type="text" class="form-control" value={this.state.ime} onChange={this.handleChange} name="ime" id="inputEmail4" placeholder="" />
								</div>
								<div class="form-grou col-md-4">
									<label for="inputPassword4">Prezime</label>
									<input type="text" class="form-control" value={this.state.prezime} onChange={this.handleChange} name="prezime" id="inputPassword4" placeholder="" />
								</div>
							
								<div class="form-group col-md-4">
									<label for="inputAddress">Email</label>
									<input type="email" class="form-control" value={this.state.email} onChange={this.handleChange} name="email" id="inputAddress" placeholder="" />
								</div>
							</div>
								
							<div class="form-row">
								<div class="form-group col-md-4">
									<label for="inputAddress2">Telefon</label>
									<input type="text" class="form-control" value={this.state.telefon} onChange={this.handleChange} name="telefon" id="mjesto" placeholder="" />
								</div>
								<div class="form-group col-md-4">
									<label for="inputCity">Mjesto</label>
									<input type="text" class="form-control" value={this.state.grad} onChange={this.handleChange} name="grad" id="inputCity" />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Adresa</label>
									<input id="inputState" name="adresa" value={this.state.adresa} onChange={this.handleChange} class="form-control" />
								</div>
							</div>
							<h2>Podaci o preminulom</h2>
							<div class="form-row">
								<div class="form-group col-md-4">
									<label for="inputEmail4">Ime</label>
									<input type="email" class="form-control" value={this.state.premIme} onChange={this.handleChange} name="premIme" id="inputEmail4" placeholder="" />
								</div>
								<div class="form-group col-md-4">
									<label for="inputPassword4">Prezime</label>
									<input type="text" class="form-control" value={this.state.premPrezime} onChange={this.handleChange} name="premPrezime" id="inputPassword4" placeholder="" />
								</div>
							
								<div class="form-group col-md-4">
									<label for="inputAddress">Datum rođenja</label>
									{ !this.state.dat1set ?
										<input type="date" class="form-control" name="datumRodj" onChange={this.handleDate} />
									:
										<div>
											<input type="text" class="form-control" name="datumRodj" value={this.state.dat1} readonly /><i onClick={this.handleClick} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
										</div>
									}
								</div>
							</div>
								
							<div class="form-row">
								<div class="form-group col-md-4">
									<label for="inputAddress2">Datum smrti</label>
									{ !this.state.dat2set ?
										<input type="date" className="form-control" name="datumSmrti" onChange={this.handleDate2} />
									:
										<div class="div">
											<input type="text" className="form-control" name="datumSmrti" value={this.state.dat2} readonly /><i onClick={this.handleClick2} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
										</div>
									}
								</div>
								<div class="form-group col-md-4">
									<label for="inputCity">Mjesto rođenja</label>
									<input type="text" class="form-control" value={this.state.mjRodj} onChange={this.handleChange} name="mjRodj" id="inputCity" />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Pogrebno mjesto</label>
									<input id="inputState" name="mjSmrti" value={this.state.mjSmrti} onChange={this.handleChange} class="form-control" />
								</div>
							</div>
							<div className="form-row">
								<div class="form-group col-md-4">
									<label for="inputState">Religija</label>
									<select id="inputState" value={this.state.religija} onChange={this.handleReligija} name="religija" class="form-control" >
										<option value="Islam">Islam</option>
										<option value="Kršćanstvo">Kršćanstvo</option>
										<option value="Juadizam">Juadizam</option>
										<option value="Ateizam">Ateizam</option>
										<option value="Ostali">Ostali</option>
									</select>
								</div>
							</div>
							<br />
							<input type="submit" class="btn btn-primary" onClick={this.naruci} value="Naruči" />
						</form>
					</div>
				</div>
				<Footer />
   		 	</div>	
		);
	}
}


