import React from 'react';
import './uspomena.css';
import Header from '../HeaderComponent/header';
import Api from '../api';
import Footer from '../footer/Fotter';

export default class Uspomena extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			ime: '',
			prezime: "",
			dRodjenja: '',
			dSmrti: '',
			mRodjenja: "",
			mSmrti: "",
			vjera: "",
			slika: [],
			biografija: '',
		}
	}

	componentDidMount() {
		let self = this;
		console.log(this.props.match.params.id);
		Api.get("api/oneMemory/" + this.props.match.params.id)
		.then(res => {
			if(res.status === 200) {
				const datumr = new Date(res.data.memory.deceased.dateOfBirth);
				const datums = new Date(res.data.memory.deceased.dateOfDeath);
				self.setState({
					ime: res.data.memory.deceased.name.first,
					prezime: res.data.memory.deceased.name.last,
					dRodjenja: datumr.toLocaleDateString(),
					dSmrti: datums.toLocaleDateString(),
					mRodjenja: res.data.memory.deceased.birthPlace,
					mSmrti: res.data.memory.deceased.placeOfDeath,
					vjera: res.data.memory.deceased.religion,
					biografija: res.data.memory.deceased.biography,
					slika: res.data.memory.deceased.photo,
				});
			}
			else {
				alert("Greska");
			}
		})
		.catch(err => {
			alert("Greska");
		});
	}

	render() {
		return(
			<div>
				<div className="USPOMENA">
					<div>
						<Header />
					</div>
					<div style={{height: "100vh", marginTop: "160px"}} >
						
						<div className="detalji" >
							
							<h1 className="nas2">ime{this.state.ime} {this.state.prezime}</h1>
							<br/>
							<br/>
							<div className="detalji2" >
								<div style={{ width: "30%" }} >
									<img style={{ marginTop: "25px", marginBottom: "25px", width: "250px", height: "250px" }} className="kSlika" src={`http://localhost:8000/${this.state.slika[0]}`} alt="First slide" />		
								</div>
								<div className="listadetalja">
									<ul>
										<li className="detalj">Godina rodjenja: {this.state.dRodjenja}</li>
										<li className="detalj">Godina smrti: {this.state.dSmrti}</li>
										<li className="detalj">Mjesto rodjenja: {this.state.mRodjenja}</li>
										<li className="detalj">Mjesto smrti: {this.state.mSmrti}</li>
										<li className="detalj">Vjeroispovjest: {this.state.vjera}</li>
									</ul>
								</div>
							</div>
							<div className="biografija">
								<br />
								<h2>Biografija:</h2>
								<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
							</div>
							<div id="VIDEO">
								<h2>Video</h2>
								<div class="YOUTUBE">
									<iframe id="YOU" src={`https://www.youtube.com/embed/${this.state.youtubeVideo}`} style={{ height: "300px", width: "500px" }} ></iframe>
								</div>
							</div>
							<div>
								<br />
								<h2 style={{marginBottom: "25px"}} >Galerija</h2>
								<div className="container">
									<div className="row galerija" style={{ marginLeft: "4%" }} >
										{this.state.slika.map(slika => {
										return <div className="col-xs-12 col-sm-6 col-md-4 col-lg-4 col-xl-3"><img alt="Slika" src={`http://node.wereact.de/${slika}`} style={{ width: "200px", height: "200px", marginTop: "20px", marginBottom: "50px" }} /></div>
										})}
									</div>
								</div>
								<Footer />
							</div>
						</div>
					</div>
				</div>
				<div>
				</div>
			</div>
		);
	}
}


