import React from 'react';
import axios from 'axios';
import Api from './api';
import { Link } from 'react-router-dom';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import $ from 'jquery';

import 'react-datepicker/dist/react-datepicker.css';

import '../Styles/promjenaSifre.css';

export default class EditujUspomenu extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      ime: '',
      prezime: '',
      telefon: '',
      email: '',
      grad: '',
      adresa: '',
      premIme: '',
      premPrezime: '',
      biografija: '',
      religija: '',
      datumRodj: moment(),
      datumSmrti: '',
      mjRodj: '',
      mjSmrti: '',
      photo: [],
      photos: [],
      premium: true,
      datumPlacanja: null,
      komentar: '',
      datumEdit: null,
      note: '',
      youtubeVideo: '',
      dat1: '',
      dat1set: true,
      dat2: '',
      dat2set: true,
    }

    this.editujIzmjenu = this.editujIzmjenu.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleCheckbox = this.handleCheckbox.bind(this);
    this.dated = this.dated.bind(this);
    this.handlePicture = this.handlePicture.bind(this);
    this.handleMultiplePictures = this.handleMultiplePictures.bind(this);
    this.glavnaSlika = this.glavnaSlika.bind(this);
    this.obrisiSliku = this.obrisiSliku.bind(this);
    this.handleReligija = this.handleReligija.bind(this);
    this.handleVideo = this.handleVideo.bind(this);
  }

  handleClick = () => {
    this.setState({ dat1set: false });
  }

  handleClick2 = () => {
    this.setState({ dat2set: false });
  }

  handleDate = (e) => {
    this.setState({ datumRodj: e.target.value });
    let a = e.target.value;
    a = a.split('-');
    a = a.reverse();
    console.log(a);
    this.setState({
      dat1: a.join('/'),
      dat1set: true,
    });
  }

  handleDate2 = (e) => {
    this.setState({ datumSmrti: e.target.value });
    let a = e.target.value;
    a = a.split('-');
    a = a.reverse();
    console.log(a);
    this.setState({ dat2: a.join('/') });
  }

  handleVideo(e) {
    let video = e.target.value.split('=')[1].split('&')[0];
    console.log(video);
    this.setState({ youtubeVideo: video });
  }

  glavnaSlika(i) {
    let self = this;
    let niz = this.state.photo;
    if(i === 0) {
      alert("Slika vec odabrana kao istaknuta");
    }
    else {
      let temp = niz[0];
      niz[0] = this.state.photo[i];
      niz[i] = temp;
      self.setState({ photo: niz });
    }
  }

  handleReligija(e) {
    this.setState({ religija: e.target.value });
  }

  obrisiSliku(i) {
    let niz = this.state.photo;
    niz.splice(i, 1);
    this.setState({ photo: niz });
  }

  handleMultiplePictures(e) {
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    const data = new FormData();
    data.append('file', e.target.files[0]);
    /*const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'multipart/form-data'
      }
    }*/
    Api.post('api/picture', data)
    .then(res => {
      if(res.status === 200) {
				self.setState({ photo: [ ...this.state.photo, res.data.photo[0]] });
			}
    })
    .catch(err => {
      alert("Greška");
    });
  }

  handlePicture(e) {
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    const data = new FormData();
    data.append('file', e.target.files[0]);
    /*const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'multipart/form-data'
      }
    }*/
    Api.post('api/picture', data)
    .then(res => {
      if(res.status === 200) {
        self.setState({ photo: [res.data.photo[0]] });
      }
    })
    .catch(err => {
      alert("Greška");
    });
  }

  dated(e) {
  }

  handleChange(e) {
    console.log(e.target.value);
    this.setState({ [e.target.name]: e.target.value });
  }

  handleCheckbox(e) {
    this.setState({ premium: e.target.checked });
  }

  editujIzmjenu(e) {
    e.preventDefault();
    let ime = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put("api/memories/" + ime, {
      memory: {
        personalInfo: {
          name: {
            first: this.state.ime,
            last: this.state.prezime
          },
          phone: this.state.telefon,
          email: this.state.email,
          place: {
            city: this.state.grad,
            address: this.state.adresa
          }
        },
        deceased: {
          name: {
            first: this.state.premIme,
            last: this.state.premPrezime
          },
          biography: this.state.biografija,
          religion: this.state.religija,
          dateOfBirth: this.state.datumRodj,
          dateOfDeath: this.state.datumSmrti,
          birthPlace: this.state.mjRodj,
          placeOfDeath: this.state.mjSmrti,
          photo: this.state.photo,
        },
        premium: {
          isPremium: this.state.premium,
          paidAt: this.state.datumPlacanja,
        },
        note: this.state,
        comment: this.state.komentar,
      }
    })
    .then(res => {
      if(res.status === 202) {
        alert("Uspješno editovana uspomena!");
      }
      else {
        alert("Greška pri editovanju");
      }
    })
    .catch(err => {
      alert("Greška");
    });
  }

  componentDidMount() {
    let self = this;
    let id = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/oneMemory/" + id)
    .then(res => {
      let memoryOne = res.data.memory;
      self.setState({
        id: memoryOne._id,
        ime: memoryOne.personalInfo.name.first,
        prezime: memoryOne.personalInfo.name.last,
        email: memoryOne.personalInfo.email,
        telefon: memoryOne.personalInfo.phone,
        grad: memoryOne.personalInfo.place.city,
        adresa: memoryOne.personalInfo.place.address,
        premIme: memoryOne.deceased.name.first,
        premPrezime: memoryOne.deceased.name.last,
        biografija: memoryOne.deceased.biography,
        religija: memoryOne.deceased.religion,
        datumRodj: memoryOne.deceased.dateOfBirth,
        datumSmrti: memoryOne.deceased.dateOfDeath,
        mjRodj: memoryOne.deceased.birthPlace,
        mjSmrti: memoryOne.deceased.placeOfDeath,
        photo: memoryOne.deceased.photo,
        premium: memoryOne.premium.isPremium,
        datumPlacanja: memoryOne.premium.paidAt,
        komentar: memoryOne.comment,
        datumEdit: memoryOne.lastEdited,
        note: memoryOne.note,
      });
      console.log(res.data.note);
      let a = new Date(memoryOne.deceased.dateOfBirth);
      a = a.toLocaleDateString();
      a = a.split('/');
      a = a[1] + '/' + a[0] + '/' + a[2];
      this.setState({ dat1: a });
      let a2 = new Date(memoryOne.deceased.dateOfDeath);
      a2 = a2.toLocaleDateString();
      a2 = a2.split('/');
      a2 = a2[1] + '/' + a2[0] + '/' + a2[2];
      this.setState({ dat2: a2 });
    })
    .catch(err => {
      alert("Greška");
    });

  }

  render() {
    return(
      <div className="sifra" style={{ marginTop: "5%" }} >
        <form onSubmit={this.editujIzmjenu} >
          <h1>Podaci o naručiocu</h1>
          <label>Ime</label>
          <input type="text" onChange={this.handleChange} name="ime" value={this.state.ime} />
          <label>Prezime</label>
          <input type="text" onChange={this.handleChange} name="prezime" value={this.state.prezime} />
          <label>Telefon</label>
          <input type="text" onChange={this.handleChange} name="telefon" value={this.state.telefon} />
          <label>Email</label>
          <input type="text" onChange={this.handleChange} name="email" value={this.state.email} />
          <label>Grad</label>
          <input type="text" onChange={this.handleChange} name="grad" value={this.state.grad} />
          <label>Adresa</label>
          <input type="text" onChange={this.handleChange} name="adresa" value={this.state.adresa} />

          <h1>Podaci o preminulom</h1>
          <label>Ime</label>
          <input type="text" onChange={this.handleChange} name="premIme" value={this.state.premIme} />
          <label>Prezime</label>
          <input type="text" onChange={this.handleChange} name="premPrezime" value={this.state.premPrezime} />
          <label>Biografija</label>
          <textarea onChange={this.handleChange} name="biografija" value={this.state.premium ? this.state.biografija : ""} disabled={this.state.premium ? false : true} ></textarea>
          <label>Religija</label>
          <select value={this.state.religija} onChange={this.handleReligija} >
            <option value="Islam" >Islam</option>
            <option value="Kršćanstvo" >Kršćanstvo</option>
            <option value="Judaizam" >Judaizam</option>
            <option value="Ateizam" >Ateizam</option>
            <option value="Ostali" >Ostali</option>
          </select>
          <label>Datum rođenja</label>
          { !this.state.dat1set ?
          <input type="date" onChange={this.handleDate} />
          :
          <div>
            <input type="text" name="datumRodj" value={this.state.dat1} readonly /><i onClick={this.handleClick} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
          </div>
          }
          <label>Datum smrti</label>
          { !this.state.dat2set ?
            <input type="date" onChange={this.handleDate2} />
          :
            <div class="div">
              <input type="text" name="datumSmrti" value={this.state.dat2} readOnly /><i onClick={this.handleClick2} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
            </div>
          }
          <label>Mjesto rođenja</label>
          <input type="text" onChange={this.handleChange} name="mjRodj" value={this.state.mjRodj} />
          <label>Pogrebno mjesto</label>
          <input type="text" onChange={this.handleChange} name="mjSmrti" value={this.state.mjSmrti} />
          { this.state.photo.map((slika, i) => {
            return <div style={{ marginTop: "40px", marginBottom: "40px" }} ><Link style={ i === 0 ? { color: "#D4AF37" } : null  } to='#' className="uspDugme" onClick={() => this.glavnaSlika(i)} ><i className="fas fa-star" ></i></Link>
              <Link to='#' className="uspDugme2" onClick={() => this.obrisiSliku(i)} ><i className="fas fa-times" ></i></Link>
            <img 
              src={`http://node.wereact.de/${slika}`}
              alt="O nama"
              style={{ width: "200px", height: "200px", border: "1px solid black", borderRadius: "5px" }}
            /></div>
          })}
          <br />
          
          { !this.state.premium ?
              null
            :
              <div class="div">
                <label>Odaberite slike</label>
                <label>Napomena: Ime slike do 20 karaktera!</label>
                <input type="file" onChange={this.handleMultiplePictures} />
              </div>
          }
          <br />
          { this.state.premium ?
            <div>
              <label>Video</label>
              <br />
              <input type="text" value={this.state.youtubeVideo} name="youtubeVideo" style={{ width: "100%" }} onChange={this.handleVideo} />
            </div>
          :
            null
          }
          <br />
          {this.state.youtubeVideo ?
            <iframe src={`https://www.youtube.com/embed/${this.state.youtubeVideo}`} style={{ height: "300px" }} ></iframe>
          :
            null
          }
          <h1>Podaci o računu</h1>
          <label>Status: { this.state.premium ? "Premium" : "Free" }</label>
          <input type="checkbox" name="premium" checked={this.state.premium} onChange={this.handleCheckbox} />
          <label>Datum plaćanja</label>
          <input type="text" onChange={this.handleChange} name="datumPlacanja" value={this.state.datumPlacanja} />

          <h1>Bilješke</h1>
          <label>Zabilješka</label>
          <input type="text" onChange={this.handleChange} name="note" value={this.state.premium ? this.state.note : ""} disabled={this.state.premium ? false : true} />
          <label>Komentar</label>
          <textarea onChange={this.handleChange} name="komentar" value={this.state.premium ? this.state.komentar : ""} disabled={this.state.premium ? false : true} ></textarea>
          <button type="submit" className="dugme2" >Spasi izmjene</button>
        </form>
      </div>
    );
  }
}


