import React from 'react';
import axios from 'axios';
import Api from './api';
import '../Styles/promjenaSifre.css';

export default class EditujModeratora extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      username: '',
      email: ''
    }

    this.handleName = this.handleName.bind(this);
    this.handleMail = this.handleMail.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleName(e) {
    this.setState({ username: e.target.value });
  }

  handleMail(e) {
    this.setState({ email: e.target.value });
  }

  handleSubmit(e) {
    e.preventDefault();
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put('api/users/' + this.props.match.params.id, {
      user: {
        username: this.state.username,
        email: this.state.email,
        permission: "Moderator"
      }
    })
    .then(res => {
      if(res.status === 202) {
        alert("Moderator editovan!");
      }
      else {
        alert("Greška pri editovanju moderatora!");
      }
    })
    .catch(err => {
      alert("Greška");
    });
  }

  componentDidMount() {
    let vm = this;
    let ime = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/users/" + ime)
    .then(res => {
      console.log(res.data.user);
      vm.setState({
        username: res.data.user.username,
        email: res.data.user.email
      });
    })
    .catch(err => {
      alert("Greška");
    })
  }

  render() {
    return(
      <div className="sifra" >
        <form onSubmit={this.handleSubmit} >
          <label>Username</label>
          <input type="text" name="username" placeholder="Username" onChange={this.handleName} value={this.state.username} required />      
          <label>Email</label>
          <input type="email" name="email" placeholder="Email" onChange={this.handleMail} value={this.state.email} required /> 
          <button type="submit" >Edituj moderatora</button>
        </form>
      </div>
    );
  }
}


