import React from 'react';
import axios from 'axios';
import Api from './api';

import '../Styles/uspomene.css';

export default class Arhiva extends React.Component {

  constructor(props) {
    super(props);
    this.state= {
      search: '',
      focus: false,
      arhiva: [],
    }

    this.handleSearch = this.handleSearch.bind(this);
  }

  handleSearch(e) {
    this.setState({ search: e.target.value });
  }

  componentDidMount() {
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/archivedMemories")
    .then(res => {
      self.setState({
        arhiva: res.data.memory,
      });
    })
    .catch(err => {
      alert("Greška");
    });
  }

  render() {
    return(
      <div className="uspomene" >
        <h1>Arhiva</h1>
        <input 
          type="text"
          placeholder="Search"
          onChange={this.handleSearch}
          className="search"
          onFocus={() => this.setState({ focus: true })}
          onBlur={() => this.setState({ focus: false })}
          style={this.state.focus ? { borderBottom: "2px solid darkblue" } : null}
        />
        <table>
          <tr>
            <td>Uspomena</td>
            <td>Naručilac</td>
            <td>Email</td>
            <td>Status</td>
          </tr>
          {this.state.arhiva.filter(
            arhiva => arhiva.deceased.name.first
              .toLowerCase()
              .includes(this.state.search.toLowerCase()) || arhiva.personalInfo.name.first
              .toLowerCase()
              .includes(this.state.search.toLowerCase()) || arhiva.personalInfo.email
              .toLowerCase()
              .includes(this.state.search.toLowerCase()) || arhiva.status
              .toLowerCase()
              .includes(this.state.search.toLowerCase())
            )
            .map((arhiva, i) => {
              return <tr key={i} >
                      <td className="ime" >{ arhiva.deceased.name.first }</td>
                      <td className="narucilac" >{ arhiva.personalInfo.name.first }</td>
                      <td className="email" >{ arhiva.personalInfo.email }</td>
                      <td className="status" >{ arhiva.status }</td>
                    </tr>
            })
          }
        </table>
      </div>
    );
  }
}


