import React from 'react';
import Header from '../HeaderComponent/header';
import Api from '../api';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Footer from '../footer/Fotter';

import './forma.css';
import { ECHILD } from 'constants';

export default class Premium extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			ime: '',
			prezime: '',
			telefon: '',
			email: '',
			grad: '',
			adresa: '',
			premIme: '',
			premPrezime: '',
			datumRodj: '',
			datumSmrti: '',
			mjRodj: '',
			mjSmrti: '',
			religija: '',
			biografija: '',
			note: '',
			youtubeVideo: '',
			komentar: '',
			premium: true,
			slike: [],
			pikpay: false,
			imePik: '',
			prezimePik: '',
			adresaPik: '',
			gradPik: '',
			zipPik: '',
			drzavaPik: '',
			telPik: '',
			emailPik: '',
			karticaPik: '',
			cvvPik: '',
			datumPik: '',
			nacinPlacanja: "",
		}

		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleMultiplePictures = this.handleMultiplePictures.bind(this);
		this.platite = this.platite.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleVideo = this.handleVideo.bind(this);
	}

	handleReligija = (e) => {
		this.setState({ religija: e.target.value });
	} 

	handleClick = () => {
		this.setState({ dat1set: false });
	}

	handleClick2 = () => {
		this.setState({ dat2set: false });
	}

	obrisiSliku(i) {
		let niz = this.state.photo;
		niz.splice(i, 1);
		this.setState({ photo: niz });
	}

	glavnaSlika(i) {
		let self = this;
		let niz = this.state.photo;
		if(i === 0) {
		  alert("Slika vec odabrana kao istaknuta");
		}
		else {
		  let temp = niz[0];
		  niz[0] = this.state.photo[i];
		  niz[i] = temp;
		  self.setState({ photo: niz });
		}
	  }

	handleDate = (e) => {
		this.setState({ datumRodj: e.target.value });
		let a = e.target.value;
		a = a.split('-');
		a = a.reverse();
		console.log(a);
		this.setState({
			dat1: a.join('/'),
			dat1set: true,
		});
	}

	handleDate2 = (e) => {
		this.setState({ datumSmrti: e.target.value });
		let a = e.target.value;
		a = a.split('-');
		a = a.reverse();
		console.log(a);
		this.setState({ dat2: a.join('/'), dat2set: true });
	}

	nacin = () => {
		this.setState({ nacinPlacanja: "PikPay" });
	}

	uplatnica = () => {
		alert("uplatnica");
	}

	prenesiPodatke = () => {
		this.setState({
			imePik: this.state.ime,
			prezimePik: this.state.prezime,
			adresaPik: this.state.adresa,
			gradPik: this.state.grad,
			telPik: this.state.telefon,
			emailPik: this.state.email,
		});
	}

	handleChange(e) {
		this.setState({ [e.target.name]: e.target.value });
	}

	handleVideo(e) {
		let video = e.target.value.split('=')[1].split('&')[0];
		console.log(video);
		this.setState({ youtubeVideo: video });
	  }

	platite(e) {
		e.preventDefault();
		this.setState({ nacinPlacanja: "" });
		let url='';
		Api.post("api/memories/new", {
			memory: {
			  personalInfo: {
				name: {
				  first: this.state.ime,
				  last: this.state.prezime
				},
				phone: this.state.telefon,
				email: this.state.email,
				place: {
				  city: this.state.grad,
				  address: this.state.adresa
				}
			  },
			  deceased: {
				name: {
				  first: this.state.premIme,
				  last: this.state.premPrezime
				},
				biography: this.state.biografija,
				religion: this.state.religija,
				dateOfBirth: this.state.datumRodj,
				dateOfDeath: this.state.datumSmrti,
				birthPlace: this.state.mjRodj,
				placeOfDeath: this.state.mjSmrti,
				photo: this.state.slike,
			  },
			  premium: {
					isPremium: this.state.premium,
					paidAt: this.state.datumPlacanja,
			  },
			  note: this.state.note,
			  comment: this.state.komentar,
			},
			info: {
				ime: this.state.imePik,
				prezime: this.state.prezimePik,
				adresa: this.state.adresaPik,
				grad: this.state.gradPik,
				postanski: this.state.zipPik,
				drzava: this.state.drzavaPik,
				telefon: this.state.telPik,
				email: this.state.emailPik,
				brojkartice: this.state.karticaPik,
				cvv: this.state.cvvPik,
				datumisteka: this.state.datumPik,
			}
		})
		.then(res => {
		if(res.status === 200) {
		}
		else {
			alert("Greska pri narucivanju");
		}
		})
		.catch(err => {
		alert("Greska");
		});

		Api.post("pay", {})
		.then(res => {
		if(res.status === 200) {
			console.log(res);
			window.location.assign(res.data.redirect);
		}
		else {
			alert("Greska pri narucivanju");
		}
		})
		.catch(err => {
		alert("Greska");
		});
		console.log(url);
		
	}

	

	handleMultiplePictures(e) {
		console.log(e.target);
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    const data = new FormData();
    data.append('file', e.target.files[0]);
    /*const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'multipart/form-data'
      }
    }*/
    Api.post('api/picture', data)
    .then(res => {
      console.log(res);
      if(res.status === 200) {
				console.log(res.data.photo[0])
				self.setState({ slike: [ ...this.state.slike, res.data.photo[0]] });
				console.log(this.state.slike);
			}
    })
    .catch(err => {
      console.log(err);
    });
	}
	
	handleSubmit(e) {
		e.preventDefault();
		console.log('submit');
	}

	

	render() {
		return(
			<div className="narucistr">
				<Header />
				<div>
					<header id="formahead">
						<h1 className="formanaslov">uspomene.ba</h1>
						<br/> <br/>
						<h1 className="narudzbausluga">Narudžba usluga</h1>

					</header>
				</div>
				<div className="formaa">
					<form>
						<h2>Podaci o naručiocu</h2>
						<div class="form-row">
							<div class="form-group col-md-4">
								<label for="inputEmail4">Ime</label>
								<input type="text" class="form-control" value={this.state.ime} onChange={this.handleChange} name="ime" id="inputEmail4" placeholder="" />
							</div>
							<div class="form-group col-md-4">
								<label for="inputPassword4">Prezime</label>
								<input type="text" class="form-control" value={this.state.prezime} onChange={this.handleChange} name="prezime" id="inputPassword4" placeholder="" />
							</div>
						
							<div class="form-group col-md-4">
								<label for="inputAddress">Email</label>
								<input type="email" class="form-control" value={this.state.email} onChange={this.handleChange} name="email" id="inputAddress" placeholder="" />
							</div>
						</div>
							
						<div class="form-row">
							<div class="form-group col-md-4">
								<label for="inputAddress2">Telefon</label>
								<input type="text" class="form-control" value={this.state.telefon} onChange={this.handleChange} name="telefon" id="mjesto" placeholder="" />
							</div>
							<div class="form-group col-md-4">
								<label for="inputCity">Mjesto</label>
								<input type="text" class="form-control" value={this.state.grad} onChange={this.handleChange} name="grad" id="inputCity" />
							</div>
							<div class="form-group col-md-4">
								<label for="inputState">Adresa</label>
								<input id="inputState" name="adresa" value={this.state.adresa} onChange={this.handleChange} class="form-control" />
							</div>
						</div>
						<h2>Podaci o preminulom</h2>
						<div class="form-row">
							<div class="form-group col-md-4">
								<label for="inputEmail4">Ime</label>
								<input type="email" class="form-control" value={this.state.premIme} onChange={this.handleChange} name="premIme" id="inputEmail4" placeholder="" />
							</div>
							<div class="form-group col-md-4">
								<label for="inputPassword4">Prezime</label>
								<input type="text" class="form-control" value={this.state.premPrezime} onChange={this.handleChange} name="premPrezime" id="inputPassword4" placeholder="" />
							</div>
						
							<div class="form-group col-md-4">
								<label for="inputAddress">Datum rođenja</label>
								{ !this.state.dat1set ?
									<input type="date" class="form-control" name="datumRodj" onChange={this.handleDate} />
								:
									<div>
										<input type="text" class="form-control" name="datumRodj" value={this.state.dat1} readonly /><i onClick={this.handleClick} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
									</div>
								}
							</div>
						</div>
							
						<div class="form-row">
							<div class="form-group col-md-4">
								<label for="inputAddress2">Datum smrti</label>
								{ !this.state.dat2set ?
									<input type="date" className="form-control" name="datumSmrti" onChange={this.handleDate2} />
								:
									<div class="div">
										<input type="text" className="form-control" name="datumSmrti" value={this.state.dat2} readonly /><i onClick={this.handleClick2} style={{ color: "red", marginLeft: "5px" }} className="fas fa-times"></i>
									</div>
								}
							</div>
							<div class="form-group col-md-4">
								<label for="inputCity">Mjesto rođenja</label>
								<input type="text" class="form-control" value={this.state.mjRodj} onChange={this.handleChange} name="mjRodj" id="inputCity" />
							</div>
							<div class="form-group col-md-4">
								<label for="inputState">Pogrebno mjesto</label>
								<input id="inputState" name="mjSmrti" value={this.state.mjSmrti} onChange={this.handleChange} class="form-control" />
							</div>
						</div>
						<div className="form-row">
							<div class="form-group col-md-4">
								<label for="inputState">Religija</label>
								<select id="inputState" value={this.state.religija} onChange={this.handleReligija} name="religija" class="form-control" >
									<option value="Islam">Islam</option>
									<option value="Kršćanstvo">Kršćanstvo</option>
									<option value="Juadizam">Juadizam</option>
									<option value="Ateizam">Ateizam</option>
									<option value="Ostali">Ostali</option>
								</select>
							</div>
						</div>
						<div className="form-row">
							<div class="form-group col-md-4">
								<label for="inputState">Biografija</label>
								<textarea id="inputState" class="form-control" name="biografija" onChange={this.handleChange} />
							</div>
							<div class="form-group col-md-4">
								<label for="inputState">Napomena</label>
								<textarea id="inputState" class="form-control" name="note" onChange={this.handleChange} /><br/>
							</div>
							<div class="form-group col-md-4">
								<label for="inputState">Komentar</label>
								<textarea id="inputState" class="form-control" name="komentar" onChange={this.handleChange} /><br/>
							</div>
						</div>
							<label>Odaberite željene slike</label>
							<br />
							<input type="file" onChange={this.handleMultiplePictures} className="col-md-3" />								
							
						{this.state.slike.map((slike, i) => {
								return <div style={{ marginTop: "40px", marginBottom: "40px" }} >
									
									<img src={`http://node.wereact.de/${slike}`} style={{ width: "200px", height: "200px", marginTop: "20px" }}  /><br />
								</div>
							})}
						<br />
						<div className="form-row">
							<div className="col-md-4" >
								<label>Odaberite video</label>
								<br />
								<input type="text" className="form-control SLIKA" value={this.state.youtubeVideo} name="youtubeVideo" onChange={this.handleVideo} />								
							</div>
						</div>
						<br />
						{this.state.youtubeVideo ?
							<iframe src={`https://www.youtube.com/embed/${this.state.youtubeVideo}`} style={{ height: "300px", width: "500px" }} ></iframe>
						:
							null
						}
						<br />
						<hr />
						<h1>Način plaćanja</h1>
						<input type="Submit" value="PayPal" className="btn btn-primary PREMIUM" onClick={this.platite} />
						<input type="Submit" value="PikPay" className="btn btn-primary PREMIUM" onClick={this.nacin} />
						<input type="Submit" value="Upaltnica" className="btn btn-primary PREMIUM" onClick={this.uplatnica} />
						<hr />
						
						<div>
						{ this.state.nacinPlacanja === "PikPay" ?
							(<div>
								<h2>Webpay</h2><button className="btn btn-primary" onClick={this.prenesiPodatke}>Prenesite podatke</button>
							<div className="form-row">
								<div class="form-group col-md-4">
									<label for="inputState">Ime</label>
									<input type="text" value={this.state.imePik} id="inputState" class="form-control" name="imePik" onChange={this.handleChange} required />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Prezime</label>
									<input type="text" value={this.state.prezimePik} id="inputState" class="form-control" name="prezimePik" onChange={this.handleChange} required /><br/>
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Adresa</label>
									<input type="text" value={this.state.adresaPik} name="adresaPik" id="inputState" class="form-control" onChange={this.handleChange} required /><br/>
								</div>
							</div>
							<div className="form-row">
								<div class="form-group col-md-4">
									<label for="inputState">Grad</label>
									<input type="text" value={this.state.gradPik} id="inputState" class="form-control" name="gradPik" onChange={this.handleChange} required />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Poštanski broj</label>
									<input type="text" id="inputState" class="form-control" name="zipPik" onChange={this.handleChange} required /><br/>
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Država</label>
									<input type="text" name="drzavaPik" id="inputState" class="form-control" onChange={this.handleChange} required /><br/>
								</div>
							</div>
							<div className="form-row">
								<div class="form-group col-md-4">
									<label for="inputState">Telefon</label>
									<input type="text" value={this.state.telPik} id="inputState" class="form-control" name="telPik" onChange={this.handleChange} required />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Email</label>
									<input type="text" value={this.state.emailPik} id="inputState" class="form-control" name="emailPik" onChange={this.handleChange} required /><br/>
								</div>
							</div>
							<hr />
							<div className="form-row">
								<div class="form-group col-md-4">
									<label for="inputState">Broj kartice</label>
									<input type="text" name="karticaPik" id="inputState" class="form-control" onChange={this.handleChange} required /><br/>
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">CVV</label>
									<input type="text" id="inputState" class="form-control" name="cvvPik" onChange={this.handleChange} required />
								</div>
								<div class="form-group col-md-4">
									<label for="inputState">Datum isteka</label>
									<input type="text" id="inputState" class="form-control" name="datumPik" onChange={this.handleChange} required /><br/>
								</div>
							</div>
							<input type="Submit" value="Platite" className="btn btn-primary" onClick={this.platite} /></div>)
						:
							null
						}
						</div>
					</form>
				</div>
				<Footer />
			</div>
				
		);
	}
}


