import React from 'react';
import { Link } from 'react-router-dom';
import Api from '../api';

import Header from '../HeaderComponent/header';
import Footer from '../footer/Fotter';

import './pregledUspomena.css';

export default class PregledUspomena extends React.Component {
	
	constructor(props) {
		super(props);

		this.state = {
			preminuli: [],
			search: '',
			prezime: '',
			godinaRodjenja: '',
			godinaSmrti: "",
			mjestoRodjenja: '',
			mjestoSmrti: '',
			vjera: '',
		}

		this.handleSearch = this.handleSearch.bind(this);
		this.handleGodina = this.handleGodina.bind(this);
		this.handlePrezime = this.handlePrezime.bind(this);
		this.handleSmrti = this.handleSmrti.bind(this);
		this.handleMjRodjenja = this.handleMjRodjenja.bind(this);
		this.handleMjSmrti = this.handleMjSmrti.bind(this);
		this.handleVjera = this.handleVjera.bind(this);
		this.handleFilter = this.handleFilter.bind(this);
	}

	handleFilter() {
		let self = this;
		let datumRodj = '';
		let datumSmrti = '';
		if(this.state.godinaSmrti !== ''){
			datumSmrti =new Date(this.state.godinaSmrti);
			datumSmrti = datumSmrti.toISOString();
		}
		if (this.state.godinaRodjenja !== ''){
			datumRodj =new Date(this.state.godinaRodjenja);
			datumRodj = datumRodj.toISOString();
		}
		console.log(datumRodj);
		Api.post("api/filterMemories", {
			filter: [
				{
					'deceased.name.first': this.state.search
				},
				{
					'deceased.name.last': this.state.prezime
				},
				{
					'deceased.religion': this.state.vjera
				},
				{
					'deceased.placeOfDeath': this.state.mjestoSmrti
				},
				{
					'deceased.birthPlace': this.state.mjestoRodjenja
				},
				{
					'deceased.dateOfBirth': datumRodj
				},
				{
					'deceased.dateOfDeath': datumSmrti
				}
			]
		}).then(res => {
			if(res.status === 200) {
				console.log(res);

				if(res.data.length === 0) {
					alert("Nema zeljenih uspomena!");
				}
				else {
					self.setState({ preminuli: res.data });
				}
			}
			else{
				console.log(res);
			}
		}).catch(err => {
			alert('Greška');
		});
	}

	reload = () => {
		window.location.reload();
	}

	handleVjera(e) {
		this.setState({ vjera: e.target.value });
	}

	handleMjSmrti(e) {
		this.setState({ mjestoSmrti: e.target.value });
	}

	handleMjRodjenja(e) {
		this.setState({ mjestoRodjenja: e.target.value });
	}

	handleSmrti(e) {
		//if(e.target.value === '') {
		//	console.log("dsa");
		//	this.setState({ godinaSmrti: 5000 });
		//}
		//else {
		 this.setState({ godinaSmrti: e.target.value});
		//}
	}

	handlePrezime(e) {
		this.setState({ prezime: e.target.value });	
	}

	handleGodina(e) {
		//if(e.target.value === '') {
			//this.setState({ godinaRodjenja: 0 });
		//}
		//else {
			this.setState({ godinaRodjenja: e.target.value });
		//}
	}

	handleSearch(e) {
		this.setState({ search: e.target.value });
	}

	componentDidMount() {
		let self = this;
		let d1, d2;
		Api.get("api/approvedMemories")
		.then(res => {
			let a = res.data.memory;
			self.setState({ preminuli: a });
		})
		.catch(err => {
			alert("Greska");
		});
	}

	render() {
		return(
			<div className="PREGLED"><div><Header /></div>
			<div style={{width: "100%"}} className="pregledUspomena" >
				
				<h1 style={{textAlign: 'center', marginTop: "160px", paddingTop: "4%"}} >Uspomene</h1>
				<button id="SearchMeni">meni</button>
				<div className="section umrli" >
					<div className="pretraga" >
						<label>Ime</label>
						<input
							type="text"
							placeholder="Ime"
							onChange={this.handleSearch}
						/>
						{'Prezime'}
						<input 
							type="text"
							onChange={this.handlePrezime}
							placeholder="Prezime"
						/>
						{'Vjeroispovjest'}
						<input 
							type="text"
							placeholder ="Vjeroispovjest"
							onChange={this.handleVjera}
						/>
						{'Mjesto rodjenja'}
						<input 
							type="text"
							placeholder="Mjesto rodjenja"
							onChange={this.handleMjRodjenja}
						/>
						{'Mjesto smrti'}
						<input 
							type="text"
							placeholder="Mjesto smrti"
							onChange={this.handleMjSmrti}
						/>
						{'Datum rodjenja'}
						<br/>
						<input 
							type="date"
							//max={this.state.godinaSmrti}
							placeholder="Godina rodjenja"
							onChange={this.handleGodina}
							
						/>
						{'Datum smrti'}
						<input 
							type="date"
							//min={this.state.godinaRodjenja}
							placeholder="Godina smrti"
							onChange={this.handleSmrti}
						/>
						<div className="dugmici">
							<button className="btn btn-primary" onClick={this.handleFilter}>Filtriraj</button>
							<button className="btn btn-primary" onClick={this.reload}>Reset filter</button>
						</div>
					</div>
					<div className="pregled"  >
						{/*this.state.preminuli.filter((preminuli) => preminuli.ime.toLowerCase()
							.includes(this.state.search.toLowerCase()) && preminuli.prezime.toLowerCase()
							.includes(this.state.prezime.toLowerCase()) && preminuli.dRodjenja >= this.state.godinaRodjenja && 
							preminuli.dSmrti <= this.state.godinaSmrti && preminuli.mRodjenja.toLowerCase()
							.includes(this.state.mjestoRodjenja.toLowerCase()) && preminuli.mSmrti.toLowerCase()
							.includes(this.state.mjestoSmrti.toLowerCase()) && preminuli.vjera.toLowerCase()
							.includes(this.state.vjera.toLowerCase())
						)*/this.state.preminuli.map((prem, i) => {
							return <div key={i} className="pregledUspomene" style={{marginTop: "10px"}} >
										<div className="prUsp usp"  >
											<img className="img-responsive makdizdar" alt="Slika preminulog" src={`http://node.wereact.de/${prem.deceased.photo[0]}`} style={{ width: "200px", height: "200px", marginTop: "20px" }} />
											<h2>{prem.deceased.name.first} {prem.deceased.name.last}</h2>
										</div>
										<div className="prUsp sl2" >
											
											<h4>Rodjen: {prem.deceased.dateOfBirth}</h4>
											<h4>Preminuo: {prem.deceased.dateOfDeath}</h4>
											<h4>Rodjen: {prem.deceased.birthPlace}</h4>
											<h4>Pogrebno mjesto: {prem.deceased.placeOfDeath}</h4>
											<h4>Vjeroispovjest: {prem.deceased.religion}</h4>
											<Link className="btn btn-primary" to={`/uspomena/${prem._id}`}>Vise</Link>
										</div>
									</div>
						})}
					</div>
				</div>
			</div>
			<Footer />
			</div>
		);
	}
}


