import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Api from './api';

import '../Styles/uspomene.css';

export default class Uspomene extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      search: '',
      focus: false,
      uspomene: [],
    }

    this.deleteMemory = this.deleteMemory.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
  }

  handleSearch(e) {
    this.setState({ search: e.target.value });
  }

  deleteMemory(i) {
    console.log(i);
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.delete("api/memories/" + i)
    .then(res => {
      console.log(res);
      if(res.status === 202) {
        alert("Uspomena obrisana!");
        window.location.reload();
      }
      else {
        alert("Greska pri brisanju");
      }
    })
    .catch(err => {
      console.log(err);
    })
  }

  componentDidMount() {
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/approvedMemories")
    .then(res => {
      self.setState({ uspomene: res.data.memory });
    })
    .catch(err => {
      console.log(err);
    });
  }

  render() {
    return(
      <div className="uspomene">
        <h1>Uspomene</h1>
        <input 
          type="text" 
          placeholder="Search" 
          className="search" 
          onChange={this.handleSearch} 
          onFocus={() => this.setState({ focus: true })}
          onBlur={() => this.setState({ focus: false })}
          style={this.state.focus ? { borderBottom: "2px solid darkblue" } : null}
        />
        <table className="prviRed" >
          <tr>
            <td>Uspomena</td>
            <td>Naručilac</td>
            <td>Status</td>
            <td>Edituj</td>
            <td>Obriši</td>
          </tr>
          {this.state.uspomene.filter(
            uspomena => uspomena.deceased.name.first
            .toLowerCase()
            .includes(this.state.search.toLowerCase()) || uspomena.personalInfo.name.first
            .toLowerCase()
            .includes(this.state.search.toLowerCase()))
          .map((uspomena, i) => {
            return <tr key={i} >
                    <td className="ime" >{uspomena.deceased.name.first}</td>
                    <td className="narucilac" >{uspomena.personalInfo.name.first}</td>
                    <td>{ uspomena.premium.isPremium ? 'Premium' : 'Free' }</td>
                    <td>
                      <Link className="edit" to={`/cms/index/uspomena/${uspomena._id}`} >
                        <i className="fas fa-edit"></i>
                      </Link>
                    </td>
                    <td>
                      <button onClick={() => this.deleteMemory(uspomena._id)} ><i className="fas fa-trash"></i></button>
                    </td>
                  </tr>
          })}
        </table>
      </div>
    );
  }
}


