import React from 'react';
import { Link } from 'react-router-dom';
import Api from './api';
import axios from 'axios';
import '../Styles/narudzbe.css';

export default class Narudzbe extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      search: '',
      focus: false,
      uspomene: [],
    }

    this.handleSearch = this.handleSearch.bind(this);
    this.inputFocus = this.inputFocus.bind(this);
    this.odbij = this.odbij.bind(this);
    this.odobri = this.odobri.bind(this);
  }

  odbij(id) {
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put('api/update/' + id, {
      status: "Stornirano",
    })
    .then(res => {
      if(res.status === 200) {
        alert("Odbijeno");
      }
      window.location.reload();
    })
    .catch(err => {
      console.log(err);
    });
  }

  odobri(id) {
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put('api/update/' + id, {
      status: "Odobreno",
    })
    .then(res => {
      if(res.status === 200) {
        alert("Odobreno");
      }
      window.location.reload();
    })
    .catch(err => {
      console.log(err);
    });
  }

  handleSearch(e) {
    this.setState({ search: e.target.value });
  }

  inputFocus(e) {

  }

  componentDidMount() {
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    let self = this;
    Api.get('api/memories')
    .then(res => {
      console.log(res.data.memory);
      self.setState({ uspomene: res.data.memory });
    })
    .catch(err => {
      alert("Greška");
    });
  }

  render() {
    return(
      <div className="narudzbe">
        <h1>Narudzbe</h1>
        <input 
          type="text" 
          placeholder="Search" 
          className="search" 
          onChange={this.handleSearch} 
          onFocus={() => this.setState({ focus: true })}
          onBlur={() => this.setState({ focus: false })}
          style={this.state.focus ? { borderBottom: "2px solid darkblue" } : null}
        />
        <table className="prviRed" >
          <tr>
            <td>Uspomena</td>
            <td>Naručilac</td>
            <td>Detaljno</td>
            <td>Odobri</td>
            <td>Storniraj</td>
          </tr>
          {this.state.uspomene.filter(
              uspomena => uspomena.deceased.name.first
              .toLowerCase()
              .includes(this.state.search.toLowerCase()) || uspomena.personalInfo.name.first
              .toLowerCase()
              .includes(this.state.search.toLowerCase()))
            .map((uspomena, i) => {
              return <tr key={i} >
                      <td className="ime" >{uspomena.deceased.name.first}</td>
                      <td className="narucilac" >{uspomena.personalInfo.name.first}</td>
                      <td><Link to={`/cms/index/uspomena/${uspomena._id}`} className="detaljno" ><i className="fas fa-info" ></i></Link></td>
                      <td>
                        <button onClick={() => this.odobri(uspomena._id)}>
                          <i className="fas fa-check" style={{ fontSize: "20px" }} ></i>
                        </button>
                      </td>
                      <td>
                        <button onClick={() => this.odbij(uspomena._id)} ><i className="fas fa-times"></i></button>
                      </td>
                    </tr>
          })}
        </table>
      </div>
    );
  }
}


