import React from 'react';
import axios from 'axios';
import Api from './api';

import '../Styles/promjenaSifre.css';

export default class ONama extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      name: '',
      text: '',
      photo: '',
      pictureMore: true,
    }

    this.spasiIzmjene = this.spasiIzmjene.bind(this);
    this.handleName = this.handleName.bind(this);
    this.handleText = this.handleText.bind(this);
    this.handlePicture = this.handlePicture.bind(this);
  }

  handlePicture(e) {
    console.log(e.target);
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    const data = new FormData();
    data.append('file', e.target.files[0]);
    /*const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'multipart/form-data'
      }
    }*/
    Api.post('api/pictureAbout', data)
    .then(res => {
      console.log(res);
      if(res.status === 200) {
        self.setState({ photo: res.data.photo });
      }
    })
    .catch(err => {
      console.log(err);
    });
  }

  handleName(e) {
    this.setState({ name: e.target.value });
  }

  handleText(e) {
    this.setState({ text: e.target.value });
  }

  spasiIzmjene(e) {
    e.preventDefault();
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put("api/about/" + this.state.id, {
      about: {
        name: this.state.name,
        text: this.state.text,
        photo: this.state.photo,
      }
    })
    .then(res => {
      if(res.status === 202) {
        alert("Uspjesna izmjena");
      }
      else {
        alert('Greska pri editovanju');
      }
    })
    .catch(err => {
      console.log(err);
    })
  }

  componentDidMount() {
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/about")
    .then(res => {
      self.setState({
        id: res.data.about[0]._id,
        name: res.data.about[0].name,
        text: res.data.about[0].text,
        photo: res.data.about[0].photo,
      });
      console.log(this.state);
    })
    .catch(err => {
      alert("Greška");
    })
  }

  render() {
    return(
      <div className="sifra" >
      <h1>O nama</h1>
        <form onSubmit={this.spasiIzmjene} >
          <label>Ime firme</label>
          <input type="text" name="name" placeholder="Ime firme" onChange={this.handleName} value={this.state.name} />
          <label>Opis</label>
          <textarea name="text" id="" cols="30" rows="10" placeholder="O nama..." onChange={this.handleText} value={this.state.text} ></textarea>
          {/*<label>Izaberite sliku</label>
          <input type="file" name="file" onChange={this.handlePicture} style={!this.state.pictureMore ? { display: "none" } : null} />
          <img 
            src={`http://localhost:8000/${this.state.photo}`}
            alt="O nama"
            style={{ width: "200px", height: "200px", marginTop: "20px" }}  
          />*/}
          <button type="submit" className="dugme2">Spasi izmjene</button>
        </form>
      </div>
    );
  }
}


