import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

//import styles
import './App.css';

//components
import Login from './Components/Login';
import IndexCMS from './Components/index';
import Onama from './Components/Onama/Onama';
import Naslovna from './Components/naslovna/naslovna.js';
import Forma from './Components/Detalji/Forma';
import Narucivanje from './Components/Forma/Narucivanje';
import PregledUspomena from './Components/PregledUspomena/PregledUspomena';
import Uspomena from './Components/uspomena/Uspomena';
import Premium from './Components/Forma/Premium';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false,
    }
  }

  componentDidMount() {
    let self = this;
    let token = localStorage.getItem('jwtToken');
    if(token) {
      self.setState({ loggedIn: true });
    }
    else {
      self.setState({ loggedIn: false });
    }
  }

  render() {
    return (
      <div>
        <Router>
          <div>
            <Route exact path="/" component={Naslovna} />
            <Route exact path="/forma" component={Forma} />
            <Route exact path="/uspomene/:id" component={PregledUspomena} />
            <Route exact path="/naruci" component={Narucivanje} />
            <Route exact path="/naruciPremium" component={Premium} />
            <Route exact path="/uspomena/:id" component={Uspomena} />
            <Route exact path="/oNama" component={Onama} />
            <Route exact path='/cms' component={Login} />
            <Route path='/cms/index' component={IndexCMS} />
            </div>
        </Router>
      </div>
    );
  }
}

export default App;



