import React from 'react';
import axios from 'axios';
import Api from './api';

import '../Styles/promjenaSifre.css';

export default class EditujUspomenu extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      ime: '',
      prezime: '',
      telefon: '',
      email: '',
      grad: '',
      adresa: '',
      premIme: '',
      premPrezime: '',
      biografija: '',
      religija: '',
      datumRodj: '',
      datumSmrti: '',
      mjRodj: '',
      mjSmrti: '',
      photo: [],
      premium: true,
      datumPlacanja: null,
      komentar: '',
      datumEdit: null,
      note: '',
    }

    this.handleCheckbox = this.handleCheckbox.bind(this);
    this.dated = this.dated.bind(this);
  }

  dated(e) {
    console.log(typeof(e.target.value));
  }

  handleCheckbox(e) {
    this.setState({ premium: !this.state.premium });
  }

  componentDidMount() {
    let self = this;
    let id = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/oneMemory/" + id)
    .then(res => {
      
      console.log(Date.parse(res.data.memory.lastEdited.split('T')[0]));
      let memoryOne = res.data.memory;
      let d = new Date(memoryOne.deceased.dateOfDeath);//.toISOString());
      console.log(d.toLocaleDateString());
      let a = new Date(d.toLocaleDateString());
      console.log(a.toISOString());
      
      self.setState({
        id: memoryOne._id,
        ime: memoryOne.personalInfo.name.first,
        prezime: memoryOne.personalInfo.name.last,
        email: memoryOne.personalInfo.email,
        telefon: memoryOne.personalInfo.phone,
        grad: memoryOne.personalInfo.place.city,
        adresa: memoryOne.personalInfo.place.address,
        premIme: memoryOne.deceased.name.first,
        premPrezime: memoryOne.deceased.name.last,
        biografija: memoryOne.deceased.biography,
        religija: memoryOne.deceased.religion,
        datumRodj: memoryOne.deceased.dateOfBirth,
        datumSmrti: memoryOne.deceased.dateOfDeath,
        mjRodj: memoryOne.deceased.birthPlace,
        mjSmrti: memoryOne.deceased.placeOfDeath,
        photo: memoryOne.deceased.photo,
        premium: memoryOne.premium.isPremium,
        datumPlacanja: memoryOne.premium.paidAt,
        komentar: memoryOne.comment,
        datumEdit: memoryOne.lastEdited,
        note: memoryOne.note,
      });
    })
    .catch(err => {
      console.log(err);
    });
    console.log(this.props);
  }

  render() {
    return(
      <div className="sifra" style={{ marginTop: "5%" }} >
        <form>
          <h1>Podaci o naručiocu</h1>
          <label>Ime</label>
          <input type="text" name="ime" value={this.state.ime} />
          <label>Prezime</label>
          <input type="text" name="prezime" value={this.state.prezime} readOnly />
          <label>Telefon</label>
          <input type="text" name="telefon" value={this.state.telefon} readOnly/>
          <label>Email</label>
          <input type="text" name="email" value={this.state.email} readOnly/>
          <label>Grad</label>
          <input type="text" name="grad" value={this.state.grad} readOnly/>
          <label>Adresa</label>
          <input type="text" name="adresa" value={this.state.adresa} readOnly/>

          <h1>Podaci o preminulom</h1>
          <label>Ime</label>
          <input type="text" name="premIme" value={this.state.premIme} readOnly/>
          <label>Prezime</label>
          <input type="text" name="premPrezime" value={this.state.premPrezime} readOnly/>
          <label>Biografija</label>
          <textarea name="biografija" value={this.state.biografija} readOnly></textarea>
          <label>Religija</label>
          <input type="text" name="religija" value={this.state.religija} readOnly/>
          <label>Datum rođenja</label>
          <input type="date" onBlur={this.dated} name="datumRodj" value={this.state.datumRodj} readOnly required/>
          <label>Datum smrti</label>
          <input type="text" name="datumSmrti" value={this.state.datumSmrti} readOnly/>
          <label>Mjesto rođenja</label>
          <input type="text" name="mjRodj" value={this.state.mjRodj} readOnly/>
          <label>Datum kada je dodana</label>
          <input type="text" name="mjSmrti" value={this.state.mjSmrti} readOnly/>
          { this.state.photo.map(slika => {
            return <img 
              src={`http://node.wereact.de/${slika}`}
              alt="O nama"
              style={{ width: "200px", height: "200px", marginTop: "20px" }}  
            />
          })}
          <br />
          <h1>Podaci o računu</h1>
          <label>Status: { this.state.premium ? "Premium" : "Free" }</label>
          <label>Datum plaćanja</label>
          <input type="text" name="datumPlacanja" value={this.state.datumPlacanja} readOnly/>

          <h1>Bilješke</h1>
          <label>Zabilješka</label>
          <input type="text" name="note" value={this.state.note} readOnly/>
          <label>Komentar</label>
          <textarea name="komentar" value={this.state.komentar} readOnly style={{ marginBottom: "20%" }} ></textarea>
        </form>
      </div>
    );
  }
}


