import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './header.css';
import $ from 'jquery';

class Header extends Component {
  componentDidMount() {

    $(document).ready(function(){
      $("#slika2").css("display", "none");
    });

    var scrollTop = 0;
        $(window).on("scroll load", function(){
          scrollTop = $(window).scrollTop();
          $('.counter').html(scrollTop);

          if(scrollTop > 0) {
            $('#slika').css("display", "none");
            $("#slika2").css("display", "block");
            $(".collapse").addClass("red");
            $(".navbar").removeClass("skrol2");
            $(".navbar-toggler").addClass("toggleSlika");
          }
          else {
            $("#slika").css("display", "block");
            $("#slika2").css("display", "none");
            $(".navbar").addClass("skrol2");
            $(".navbar-toggler").removeClass("toggleSlika");
          }
        });
  }

  render() {
    return (
      <div>
        <nav className="navbar skrol2 navbar-expand-lg navbar-light">
          <div id="podaci1">
            <p>Mail: uspomene@gmail.com</p>
          </div>
          <div id="podaci2">
            <p>Broj telefona: 062000000</p>  
          </div>
          <div id="slika">
            <img src="../Slike/logofinal.png" alt="Logo" />
          </div>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <div id="slika2">
              <img src="../Slike/logofinal.png" alt="Logo" />
            </div>
            <ul className="navbar-nav" style={{margin: "none"}}>
              <li className="nav-item">
                <Link className="nav-link LINK" to="/"><span style={{ color: "black" }} >Home</span> <span className="sr-only">(current)</span></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link LINK" to="/uspomene/1"><span style={{ color: "black" }} >Uspomene</span></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link LINK" to="/forma"><span style={{ color: "black" }} >Naruči</span></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link LINK" to="/Onama"><span style={{ color: "black" }} >About</span></Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

export default Header;


